import React from 'react';
import Equation from './Equation.js';

class Demo extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			showEditor1: false,
			showEditor2: false,
			showEditor3: false
		};
		this.onToggleEditor = this.onToggleEditor.bind(this);
	}

	onToggleEditor(i,show) {
		this.setState({
			showEditor1: i === 1 && show,
			showEditor2: i === 2 && show,
			showEditor3: i === 3 && show,
		})
	}

	render() {
		return (
			<div>
{/*				<div className='TopButtons'>
					<a className=''><span>Source</span></a>
					<a className='selected'>Rich Text</a>
				</div>*/}
				<div className='Latex'>
					<span>The Uniformly Accelerated Motion formula is defined by </span>
			        <Equation initialValue='d=v * t + at^2/2' showEditor={this.state.showEditor1} onToggleEditor={(e)=>this.onToggleEditor(1,e)}/><span>, where
			        <Equation initialValue="at^2/3" showEditor={this.state.showEditor2} onToggleEditor={(e)=>this.onToggleEditor(2,e)}/> corresponds to the acceleration factor.
			        </span><br/><br/>
			        <span>Other examples of equations in physics include </span>
			        <Equation initialValue='E=M*C^3' showEditor={this.state.showEditor3} onToggleEditor={(e)=>this.onToggleEditor(3,e)}/>
			        <span>, which is maybe the most famous of all!</span>
		        </div>
	        </div>
		);
	}
}

export default Demo;
