import React from 'react';
import 'katex/dist/katex.min.css';
import Latex from 'react-latex-next';

function convertEquationToLatex(equation) {
	// Supported features: a/b -> \frac{a}{b}
	// ( and ) turn into \( and \)

	// 1. We parse the equation (string) into tokens
	const tokens = [];
	let current_token = '';
	for (let i = 0; i < equation.length; i++) {
		const c = equation[i];
		if (c == '/') {
			tokens.push(current_token);
			tokens.push('/');
			current_token = '';
		}
		else if (c == '+') {
			tokens.push(current_token);
			tokens.push(c);
			current_token = ''
		}
		else if (c == '*') {
			tokens.push(current_token);
			tokens.push('\\cdot');
			current_token = '';
		}
		else if (c == ' ') {
			tokens.push(current_token);
			current_token = '';
		}
		else {
			current_token += c;
		}
	}
	if (current_token != '') {
		tokens.push(current_token);
	}

	// 2. 
	for (let i = 0; i < tokens.length; i++) {
		if (i<tokens.length -2 && tokens[i+1] == '/') {
			tokens.splice(i, 3, '\\frac{' + tokens[i] + '\}\{' + tokens[i+2] +'}');
			i--; // We reset i to handle multiple fractions
		}
	}


	return " $" + tokens.join(' ') + "$ ";
}

const utilizeFocus = () => {
    const ref = React.createRef()
    const setFocus = () => {ref.current &&  ref.current.focus()}

    return {setFocus, ref} 
}

class Equation extends React.Component {

	constructor(props) {
		super(props);
		const equation = props.initialValue;
		this.state = {
			value: equation,
			latex: convertEquationToLatex(equation),
			// showEditor: false,
		};
		this.handleChange = this.handleChange.bind(this);
		this.handleClick = this.handleClick.bind(this);
		this.handleClickDone = this.handleClickDone.bind(this);
		this.equationInput = utilizeFocus();
	}

	handleChange(event) {
		this.setState({
			value: event.target.value,
			latex: convertEquationToLatex(event.target.value),
		})
	}

	handleClick(event) {
		// this.setState({
		// 	showEditor: true,
		// });
		this.props.onToggleEditor(true);
		this.equationInput.setFocus();
	}

	handleClickDone(event) {
		// this.setState({
		// 	showEditor: false
		// })
		this.props.onToggleEditor(false);
		event.preventDefault();
	}

	render() {
		return (
			<div className={this.props.showEditor ? 'Equation ShowEditor': 'Equation'}>
				<a onClick={this.handleClick} >
					<Latex>{this.state.latex}</Latex>
				</a>
				<div className="EquationEditor">
				<form onSubmit={this.handleClickDone}>
					<div className="Text">Edit formula:</div>
			        <input value={this.state.value} onChange={this.handleChange} ref={this.equationInput.ref}/>
			        <div className='GeneratedLatex'>Generated LaTeX: {this.state.latex}</div>
			        <input className='Button' type='submit' value='Done' />
			        </form>
				</div>
			</div>
		);
	}
}

export default Equation;
